import styled from "styled-components"
import React, { memo } from "react"
import checkIconSrc from "../assets/icon_Check.svg"
import unCheckedIconSrc from "../assets/icon_Check-1.svg"
import { mobileSize } from "../constants/device"

type Checked = { checked: boolean }

const Wrapper = styled.label<Checked>`
  background: white;
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 80px;
  cursor: pointer;

  span {
    color: ${props =>
      props.checked ? "rgba(22, 47, 232, 1)" : "rgba(22, 47, 232, 0.3)"};
    font-size: 12px;
  }

  &:hover {
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.15);
  }

  @media (max-width: ${mobileSize}) {
    padding: 0 20px;
  }
`

const CbxWrapper = styled.div`
  display: flex;
  align-items: center;

  img {
    margin-right: 40px;
    @media (max-width: ${mobileSize}) {
      margin-right: 20px;
    }
  }
`

const CheckboxHide = styled.input`
  display: none;
`

interface PayRowProps {
  checked: boolean
  payText: string
  priceText: string
  onChange: () => void
}

export const PayRow: React.FC<PayRowProps> = memo(
  ({ checked, payText, priceText, onChange }) => (
    <Wrapper checked={checked}>
      <CbxWrapper>
        <img src={checked ? checkIconSrc : unCheckedIconSrc} alt="check icon" />
        <CheckboxHide type="checkbox" checked={checked} onChange={onChange} />
        <span>{payText}</span>
      </CbxWrapper>
      <span>{priceText}</span>
    </Wrapper>
  )
)
